import React, { Suspense, lazy } from "react"
import { Router, Switch, Route, Redirect } from "react-router-dom"
import { history } from "./history"
import { connect } from "react-redux"
import Spinner from "./components/@vuexy/spinner/Loading-spinner"
import { loginWithToken , is_session } from "./redux/actions/auth"
import { ContextLayout } from "./utility/context/Layout"
import { ToastContainer } from 'react-toastify';
import { subRooter } from './configs/vars';
import 'react-toastify/dist/ReactToastify.css';



const Login = lazy(() =>
  import("./views/pages/login/Login")
)

const Assessment = lazy(() =>
  import("./views/pages/AssessmentPage/AssessmentListPage")
)
const WorkSheet = lazy(() =>
  import("./views/pages/WorkSheet/WorkSheet")
)
const AssessmentForm = lazy(() =>
  import("./views/pages/AssessmentPage/AssessmentForm")
)
const AssignPage = lazy(() =>
  import("./views/pages/Tutor/Assign_Mark/AssignPage")
)
const MarkPage = lazy(() =>
  import("./views/pages/Tutor/Assign_Mark/MarkPage")
)
const Lesson = lazy(() =>
  import("./views/pages/LessonPage/Index")
)
const EditLesson = lazy(() =>
  import("./views/pages/LessonPage/EditLesson")
)
const Score = lazy(() =>
  import("./views/pages/Tutor/Assign_Mark/ScorePage")
)
const Report = lazy(() =>
  import("./views/pages/Tutor/ReportPage/Index")
)

//  category pages
const ListCategoriesPage = lazy(() =>
  import("./views/pages/CategoriesPages/ListCategoriesPage")
)
const CreateCategoryPage = lazy(() =>
  import("./views/pages/CategoriesPages/CreateCategoryPage")
)

//  category pages end
//  topic pages
const ListTopicsPage = lazy(() =>
  import("./views/pages/TopicPages/ListTopicsPage")
)
//  levels pages
const ListLevelsPage = lazy(() =>
  import("./views/pages/Levels/ListLevelsPage")
)
// my worksheets
const MyWorkSheets_New = lazy(() => import("./views/pages/MyWorksheets/"))


// Set Layout and Component Using App Route
const RouteConfig = ({
  component: Component,
  fullLayout,
  permission,
  user,
  ...rest
}) => (
  <Route
    {...rest}
    render={props => {
      return (
        <ContextLayout.Consumer>
          {context => {
            let LayoutTag =
              fullLayout === true
                ? context.fullLayout
                : context.state.activeLayout === "horizontal"
                ? context.horizontalLayout
                : context.VerticalLayout
              return (
                <LayoutTag {...props} permission={props.user}>
                  <Suspense fallback={<Spinner />}>
                    <Component {...props} />
                  </Suspense>
                </LayoutTag>
              )
          }}
        </ContextLayout.Consumer>
      )
    }}
  />
)
const mapStateToProps = state => {
  return {
    user: state.auth.userRole
  }
}

const AppRoute = connect(mapStateToProps)(RouteConfig)

class AppRouter extends React.Component {

  UNSAFE_componentWillMount() {
    this.props.loginWithToken();
  }

  RedirectLandingPage = () => {
    return window.location.pathname='/';
  }

  render() {
    // const isDev = window.location.hostname === 'localhost';

    return (
      <Router history={history}>
        <Switch>
          {/* {
            ! isDev ? <AppRoute
              path="/"
              exact
              component={() => this.RedirectLandingPage() }
            />
            : */}
            <AppRoute
              path="/"
              exact
              component={() => <Redirect to={'/myarea/lesson'}></Redirect> }
            />
          {/* } */}
          
          <AppRoute
            exact
            path="/myarea"
            component={() => <Redirect to={'/myarea/lesson'} />}
          />

          <AppRoute
            exact
            path={`${subRooter}/lesson`}
            component={Lesson}
          />
          {/* categoris pages */}
          <AppRoute
            exact
            path={`${subRooter}/subject`}
            component={ListCategoriesPage}
          />
          <AppRoute
            exact
            path={`${subRooter}/subject/create-new`}
            component={CreateCategoryPage}
          />
          {/* topic pages */}
          <AppRoute
            exact
            path={`${subRooter}/topics`}
            component={ListTopicsPage}
          />
          {/* levels pages */}
          <AppRoute
            exact
            path={`${subRooter}/levels`}
            component={ListLevelsPage}
          />
          <AppRoute
            exact
            path={`${subRooter}/lesson/lesson-form`}
            component={EditLesson}
          />

          <AppRoute
            exact
            path={`${subRooter}/assessment`}
            component={Assessment}
          />
          <AppRoute
            exact
            path={`${subRooter}/worksheets`}
            component={WorkSheet}
          />
          <AppRoute
            exact
            path={`${subRooter}/assessment/form`}
            component={AssessmentForm}
          />
          {/* assign & mark start */}
          <AppRoute path={`${subRooter}/tutor/pages/assign`} component={AssignPage} />
          <AppRoute path={`${subRooter}/tutor/pages/mark`} component={MarkPage} />
          <AppRoute
            exact
            path={`${subRooter}/score`}
            component={Score}
          />
          <AppRoute
            exact
            path={`${subRooter}/report`}
            component={Report}
          />
          <AppRoute path={`${subRooter}/my-worksheets`} exact component={MyWorkSheets_New}  />
          
          <AppRoute path={`${subRooter}/login`} component={Login} fullLayout /> 
        </Switch>
        <ToastContainer />
      </Router>
    )
  }
}

const getIsLoggedIn = (state) => ({
  isLoggedIn : state.auth.isLoggedIn
})

const mapDispatchToProps = {
  loginWithToken, is_session
}

export default connect(getIsLoggedIn, mapDispatchToProps)(AppRouter)